<template lang="pug">
  .pages--my-account.row.mt-4.text-dark
    .col-4.offset-md-2
      h2.h2.my-2.mb-3 {{ $t(i+'my_info') }}

      .box.shadow.rounded.p-4
        .message.is-danger(v-if="error")
          .message-body
            | {{ error }}

        //- FIRST NAME

        b-form-group.has-text-left(:label="$t('auth.first_name')")
          b-form-input#first_name(v-model="form.first_name")

        //- LAST NAME

        b-form-group.has-text-left(:label="$t('last_name')")
          b-form-input#last_name(v-model="form.last_name")

        //- PHONE NUMBER

        .form-group.has-text-left
          legend.label {{$t('phone_number')}}
          phone-number-verification(v-model="form.phone_number")

        //- EMAIL

        b-form-group.has-text-left(:label="$t('auth.email')")
          b-form-input#email(
            v-model="form.email"
            type="email"
            size="is-medium"
            placeholder="Email")

        //- PASSWORD

        b-form-group.has-text-left(:label="$t('new_password')")
          b-form-input#password(v-model="form.newPassword" type="password" size="is-medium")

        //- ANALYTICS

        b-form-group.has-text-left
          b-form-checkbox(
            name='analytics'
            v-model="form.has_analytics"
            size="is-medium"
          )
            | {{ $t('help_to_improve_by_accepting_analytics') }}
      .text-right
        b-button#btn_save_profile.mt-3(
          size="sm"
          :class="{'is-loading': isLoading, 'is-disabled': isLoading, 'btn-success': isSuccess}"
          variant="primary"
          @click.stop.prevent="save")
          i.mdi.m-0(v-if="isSuccess") check
          span(v-else="")
            | Save

    .col-4
      h2.h2.my-2.mb-3 {{ $t(i+'my_subscription') }}
      subscriptions-list

</template>


<script>
    import {mapGetters, mapActions} from "vuex";
    import SubscriptionsList from "../ui/subscriptions-list";
    import PhoneNumberVerification from "../ui/phone-number-verification";

    export default {
        name: "MyAccount",
        components: {PhoneNumberVerification, SubscriptionsList},
        data() {
            return {
                i: 'my_account.',
                error: null,
                isLoading: false,
                isSuccess: false,
                form: {
                    first_name: '',
                    last_name: '',
                    phone_number: '',
                    email: '',
                    has_analytics: false,
                    newPassword: null
                }
            };
        },
        computed: {
            ...mapGetters(["user"]),
            name() {
                return (
                    this.user.name ||
                    `${this.user.first_name || ""} ${this.user.last_name || ""}`
                );
            },


        },
        created() {
            this.form = Object.assign({}, this.user)
        },
        mounted() {
            console.log(this.$route)
        },
        methods: {
            ...mapActions(['updateUser', 'setUser']),
            save() {
                this.isLoading = true;
                this.updateUser(this.form).then(res => {
                    this.setUser(res.body.data.attributes);
                    this.$auth.user(res.body.data.attributes);
                    this.isLoading = false;
                    this.isSuccess = true;
                    setTimeout(() => {
                        this.isSuccess = false;
                    }, 2500);
                });
            },


        }
    };
</script>


