  @import 'colors';

  .pages--my-account {

    fieldset {
      padding-left: 0;
      padding-right: 0;
    }

    legend,
    label:not(.classic) {
      color:       $primary;
      font-weight: 700;
      font-size:   0.9rem;
      margin-bottom: 0;
    }

    .country-selector.has-value .field-label {
      display: none;
    }
    .country-selector.has-value .field-input {
      padding-top: 3px;
    }
    .country-selector .field-country-flag {
      top: 17px;
    }

    #btn_save_profile {
      min-width: 90px;
      display: inline-flex;
      align-items: center;
      text-align: center;
      justify-content: center;
    }

    .ui--subscriptions-list {
      .box {
        /*min-height: 511px;*/
      }
    }

  }

</style>
