<template lang="pug">
  .ui--subscriptions-list
    .box.shadow.rounded.p-4
      b-form-group.has-text-left(:label="$t('offer')")
        input(id="subscription_name" :value="subscriptionName" type="text" disabled="disabled" class="form-control")

      b-form-group.has-text-left(:label="$t('valid_until')")
        input(id="subscription_validity" :value="$d(new Date(user.valid_until), 'short')" type="text" disabled="disabled" class="form-control")

    .text-right
      b-button.mt-3(
        size="sm"
        v-b-modal.offers_modal
        v-if="user.role.indexOf('pro') === -1"
        variant="primary"
        ) {{ $t('subscribe') }}

      b-button.mt-3.mr-2(
        size="sm"
        v-if="user.role.indexOf('pro') !== -1"
        variant="outline-primary"
      ) {{ $t('update_subscription') }}

      b-button.mt-3(
        size="sm"
        v-if="user.role.indexOf('pro') !== -1"
        variant="outline-danger"
      ) {{ $t('cancel_subscription') }}

    b-modal#offers_modal(hide-footer hide-header)
      template(slot="modal-header" slot-scope="{ close }") &nbsp;
      offers-list
      template(slot="modal-footer" slot-scope="{ ok, cancel }") &nbsp;

</template>

<script>
  import {mapGetters, mapActions} from "vuex";
  import OffersList from "./offers-list";

  export default {
    name: "SubscriptionsList",

    components: {
      OffersList
    },
    props: [],
    data() {
      return {
        // UX
        isLoading: false,
        querying: true,
        form: {
          subscription_name: 'Free trial'
        }
      };
    },
    computed: {
      ...mapGetters(['user', 'subscription']),
      subscriptionName() {
        if (this.user.role == 'pro') {
          return 'Pro'
        } else if (this.user.role == 'pro_plus') {
          return 'Pro+'
        } else {
          return 'Free trial'
        }
      }
    },
    created() {
      this.getSubscriptions()
    },
    mounted() {

    },
    methods: {
      ...mapActions(['getSubscriptions']),
    },
  };
</script>


