  @import 'colors';

  .ui--subscriptions-list {

  }

  #offers_modal {
    .modal-dialog {
      max-width: 1140px;
    }
    .modal-header {
      /*display: none;*/
    }
    .section-inner {
      padding:  0 !important;
    }
  }

</style>
